import { useState } from 'react';
import './App.css';
// import About from './components/About';
import Alert from './components/Alert';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
import React from 'react';

// import { 
//   BrowserRouter as 
//   Router, Route, Routes,
// } from 'react-router-dom';

function App() {

  const [alert,setAlert] = useState(null);

  const showAlert = (message,type)=>{
      setAlert({
        msg: message,
        type:type
      })
      setTimeout(()=>{
        setAlert(null);
      },1500)
  }
  function clicke(){
    showAlert("Email not found","xyz@email.com")
  }
  return(
  <>
    {/* <Router> */}

    <Navbar title="Stranger" home="Home"/>
    <Alert alert={alert}/>
    <div className="container">
      {/* <Routes> */}
         {/* <Route path="/about" element={<About/>}> */}
         {/* </Route> */}
         {/* <Route path="/textform" element={ */}
         <TextForm heading="Enter text" showAlert={showAlert}/>
         {/* }> */}
         {/* </Route> */}
      {/* </Routes> */}
    </div>
      <button className='submit' type="submit" onClick={clicke}>submit</button>
      {/* </Router> */}
  </>
  );
}
export default App;
