import React, { useState } from 'react'


export default function TextForm(props) {

  const handleUpClick = ()=>{
    console.log("Uppercase clicked" + text);
    
    let newtext;
    if(text === text.toLowerCase()){
      setcon("Lowercase");
      newtext = text.toUpperCase();
      setText(newtext);
    }
    else{
      setcon("Uppercase"); 
      newtext = text.toLowerCase();
      setText(newtext);
    }
    props.showAlert("Converted","success");
  }
  const handleOnChange = (event)=>{
    console.log("on change");
    setText(event.target.value);
  }

  const [text,setText] = useState('Enter text here');
  const[con_text,setcon] = useState('Uppercase');
  // setText("Enter name here");

  return (
    <>
    <div className='container'>
    <h1>{props.heading}</h1>
<div className="mb-3">
  <label htmlFor="mybox" className="form-label"></label>
  <textarea className="form-control" value={text} onChange={handleOnChange} id="mybox" rows="8"></textarea>
</div>
    <button className="btn btn-primary" onClick={handleUpClick}>Convert To {con_text}</button>
    </div>
    <div className="container my-2">
      <h1>Your text summary</h1>
      <p>{text.split(' ').length-1} words, {text.length} characters</p>
      <p> {0.008 * text.split(" ").length-0.008} Minutes to read</p>
      <h3>Preview</h3>
      <p>{text}</p>
    </div>
    </>
  )
}
